<template>
  <v-container>
    <v-card class="mx-auto my-12">
      <v-toolbar color="primary" dark dense flat>
        <v-toolbar-title class="text-body-4">
          Predict Your CRL Rank
        </v-toolbar-title>
      </v-toolbar>
      <v-card-text>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-row class="text-center">
            <v-col cols="12" sm="6" offset-sm="3">
              <v-select
                v-model="select"
                :items="items"
                :rules="[(v) => !!v || 'Item is required']"
                label="Session"
                required
              ></v-select> </v-col
          ></v-row>
          <v-row class="text-center">
            <v-col cols="12" sm="6" offset-sm="3">
              <v-text-field
                type="number"
                v-model="percentile"
                :counter="10"
                :rules="percRules"
                label="NTA Score/Percentile"
                required
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row class="text-center">
            <v-col cols="12" sm="6" offset-sm="3">
              <v-btn
                :disabled="!valid"
                color="primary"
                class="mr-4"
                @click="submit"
              >
                Submit
              </v-btn>

              <v-btn outlined color="primary" class="mr-4" @click="reset">
                Reset
              </v-btn>
            </v-col>
          </v-row>
        </v-form>
        <br><br>
        <v-row class="text-center">
          <v-col class="mb-6">
            <v-card class="mx-auto" color="indigo" dark max-width="400">
              <v-card-title>
                 <v-col cols="12" sm="6" offset-sm="3">
                <v-icon large left> mdi-school-outline </v-icon>
                <v-icon>fas fa-edit</v-icon>
                <span class="text-h6 font-weight-bold ">Your Rank</span>
                 </v-col>
              </v-card-title>

              <v-card-text class="text-h6 font-weight-bold">
              {{ rankMessage }}
              </v-card-text>

              <v-card-actions>
                <v-col cols="12" sm="6" offset-sm="3">
                <v-rating
                  v-model="rating"
                  background-color="yellow lighten-3"
                  color="yellow"
                ></v-rating>
                </v-col>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script>
export default {
  name: "HelloWorld",

  data: () => ({
    rating: 0,
    rankMessage: "Your rank will be displayed here.",
    exam_session: true,
    your_rank: null,
    valid: true,
    percentile: "",
    students: 769589,
    percRules: [
      (v) => !!v || "Percentile is required",
      (v) => (v && v.length <= 10) || "Percentile must be less than 10 characters",
      (v) => ( v >= 0 && v <= 100 ) || "Percentile range is 0 - 100"
    ],
    email: "",
    emailRules: [
      (v) => !!v || "E-mail is required",
      (v) => /.+@.+\..+/.test(v) || "E-mail must be valid",
    ],
    select: null,
    items: ["Session 1", "Session 2"],
  }),
  /*computed: {
    rating: function () {
      return this.your_rank / this.your_rank;
    },
  },*/
  methods: {
    submit() {
      this.rankCal();
      this.rankMessage = "Expected CRL : " + this.your_rank;
      this.ratingCal(this.your_rank);
      this.$refs.form.validate();
    },
    reset() {
      this.your_rank = null;
      this.rating = 0;
      this.rankMessage = "Your rank will be displayed here.";
      this.$refs.form.reset();
      this.$refs.form.resetValidation();
    },
    rankCal(){
      if(Number(this.percentile) === 100){
        this.your_rank = 1;
      }else{
      this.your_rank = Math.round(
        (100 - this.percentile) * (this.students / 100)
      );
      }
    },
    ratingCal(rank) {
      console.log(rank);
      if (rank < 1000) {
        this.rating = 5;
      } else if (rank < 5000) {
        this.rating = 4;
      } else if (rank < 50000) {
        this.rating = 3;
      } else if (rank < 150000) {
        this.rating = 2;
      } else if (rank < 300000) {
        this.rating = 1;
      } else {
        this.rating = 0;
      }},
  },
};
</script>
